import React, { useState, useEffect, useContext } from "react";
import axios from "axios";
import { AuthContext } from "@/App";
import { useNavigate } from "react-router-dom";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { toast } from "sonner";
import { ArrowLeft, Plus, Trash2, Users, Film, MessageSquare } from "lucide-react";

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API = `${BACKEND_URL}/api`;

const AdminPanel = () => {
  const { user } = useContext(AuthContext);
  const navigate = useNavigate();
  const [users, setUsers] = useState([]);
  const [content, setContent] = useState([]);
  const [reviews, setReviews] = useState([]);
  const [searchQuery, setSearchQuery] = useState("");
  const [contentType, setContentType] = useState("movie");
  const [searchResults, setSearchResults] = useState([]);
  const [searching, setSearching] = useState(false);
  const [existingFilter, setExistingFilter] = useState("");

  // Estados para importar desde carpeta
  const [folderEntries, setFolderEntries] = useState([]); // [{name, path}]
  const [pendingTmdbSearch, setPendingTmdbSearch] = useState(null); // nombre a buscar
  const [tmdbSearchResults, setTmdbSearchResults] = useState([]);
  const [tmdbSearchLoading, setTmdbSearchLoading] = useState(false);
  const [importContentType, setImportContentType] = useState("movie");

  useEffect(() => {
    if (!user || user.role !== "admin") {
      navigate("/");
      return;
    }
    fetchUsers();
    fetchContent();
    fetchReviews();
    }, [user, navigate]);

  const fetchUsers = async () => {
    try {
      const response = await axios.get(`${API}/admin/users`);
      setUsers(response.data);
    } catch (error) {
      console.error("Error fetching users", error);
    }
  };

  const fetchContent = async () => {
    try {
      const response = await axios.get(`${API}/content`);
      setContent(response.data);
    } catch (error) {
      console.error("Error fetching content", error);
    }
  };

  const fetchReviews = async () => {
    try {
      const response = await axios.get(`${API}/admin/reviews`);
      setReviews(response.data);
    } catch (error) {
      console.error("Error fetching reviews", error);
    }
  };

  const handleSearchContent = async () => {
    if (!searchQuery.trim()) {
      toast.error("Por favor ingresa un nombre para buscar");
      return;
    }

    setSearching(true);
    try {
      const response = await axios.post(`${API}/content/search`, {
        query: searchQuery,
        content_type: contentType,
      });
      setSearchResults(response.data.results);
      if (response.data.results.length === 0) {
        toast.info("No se encontraron resultados");
      }
    } catch (error) {
      toast.error("Error al buscar contenido");
    } finally {
      setSearching(false);
    }
  };

  const handleAddContent = async (tmdbId) => {
    try {
      await axios.post(`${API}/content`, {
        tmdb_id: tmdbId,
        content_type: contentType,
      });
      toast.success("Contenido agregado exitosamente");
      setSearchQuery("");
      setSearchResults([]);
      fetchContent();
    } catch (error) {
      toast.error(error.response?.data?.detail || "Error al agregar contenido");
    }
  };

  const handleDeleteContent = async (contentId) => {
    try {
      await axios.delete(`${API}/content/${contentId}`);
      toast.success("Contenido eliminado");
      fetchContent();
    } catch (error) {
      toast.error("Error al eliminar contenido");
    }
  };

  const handleUpdateUserRole = async (userId, newRole) => {
    try {
      await axios.patch(`${API}/admin/users/${userId}/role?role=${newRole}`);
      toast.success("Rol actualizado");
      fetchUsers();
    } catch (error) {
      toast.error("Error al actualizar rol");
    }
  };

  const handleDeleteUser = async (userId) => {
    try {
      await axios.delete(`${API}/admin/users/${userId}`);
      toast.success("Usuario eliminado");
      fetchUsers();
    } catch (error) {
      toast.error("Error al eliminar usuario");
    }
  };

  const handleDeleteReview = async (reviewId) => {
    try {
      await axios.delete(`${API}/reviews/${reviewId}`);
      toast.success("Reseña eliminada");
      fetchReviews();
    } catch (error) {
      toast.error("Error al eliminar reseña");
    }
  };

  // Maneja la selección de carpeta y obtiene los nombres únicos de carpetas/archivos LIMPIOS
  const handleFolderSelect = (e) => {
    const files = Array.from(e.target.files || []);
    // Extraer los nombres de las carpetas principales (primer segmento del path)
    const folderNames = Array.from(new Set(files.map(f => f.webkitRelativePath.split('/')[1] || f.name)));
    // Limpiar nombres: quitar paréntesis y lo que sigue, quitar T1, T2, etc., quitar corchetes y lo que sigue de un guion
    const cleanName = (name) => {
      let cleaned = name.replace(/\(.*?\).*/, ""); // Quita paréntesis y lo que sigue
      cleaned = cleaned.replace(/T\d+.*$/i, ""); // Quita T1, T2, etc. y lo que sigue
      cleaned = cleaned.replace(/\[.*?\].*/, ""); // Quita corchetes y lo que sigue
      cleaned = cleaned.replace(/-.*/, ""); // Quita guion y lo que sigue
      cleaned = cleaned.replace(/\[/g, ""); // Quita cualquier '[' sobrante
      return cleaned.trim();
    };
    const uniqueCleaned = Array.from(new Set(folderNames.map(cleanName)));
    setFolderEntries(uniqueCleaned.map(name => ({ name })));
  };

  // Buscar en TMDB por nombre (usando el tipo de importación)
  const searchTmdbByName = async (name) => {
    setTmdbSearchLoading(true);
    setPendingTmdbSearch(name);
    try {
      const response = await axios.post(`${API}/content/search`, {
        query: name,
        content_type: importContentType,
      });
      setTmdbSearchResults(response.data.results);
    } catch (error) {
      toast.error("Error al buscar en TMDB");
      setTmdbSearchResults([]);
    } finally {
      setTmdbSearchLoading(false);
    }
  };

  // Importar el contenido seleccionado de TMDB (usando el tipo de importación)
  const handleImportFromTmdb = async (tmdbId) => {
    try {
      await axios.post(`${API}/content`, {
        tmdb_id: tmdbId,
        content_type: importContentType,
      });
      toast.success("Contenido importado exitosamente");
      setPendingTmdbSearch(null);
      setTmdbSearchResults([]);
      fetchContent();
    } catch (error) {
      toast.error(error.response?.data?.detail || "Error al importar contenido");
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-blue-900 to-slate-900 px-4 py-8 sm:p-8">
      <div className="container mx-auto">
        <div className="flex flex-row items-center justify-between mb-8 gap-4 transition-all duration-200 ease-in-out flex-wrap">
          <h1 className="hidden sm:block text-3xl sm:text-4xl font-bold bg-gradient-to-r from-purple-400 to-pink-400 bg-clip-text text-transparent truncate max-w-xs">
            Panel de Administración
          </h1>
          <Button
            data-testid="back-to-home-button"
            onClick={() => navigate("/")}
            variant="outline"
            className="flex items-center justify-center border-white/20 text-white hover:bg-white/10 px-2 py-2 rounded"
          >
            <ArrowLeft className="w-4 h-4 sm:mr-2 mr-0" />
            <span className="hidden sm:inline">Volver al Inicio</span>
          </Button>
        </div>

        <Tabs defaultValue="content" className="space-y-6">
          <TabsList className="bg-white/5 border border-white/10 flex gap-2 overflow-x-auto no-scrollbar py-1 px-1">
            <TabsTrigger value="content" data-testid="content-tab" className="flex items-center gap-2 min-w-0">
              <Film className="w-4 h-4" />
              <span className="hidden sm:inline truncate">Contenido</span>
            </TabsTrigger>
            <TabsTrigger value="users" data-testid="users-tab" className="flex items-center gap-2 min-w-0">
              <Users className="w-4 h-4" />
              <span className="hidden sm:inline truncate">Usuarios</span>
            </TabsTrigger>
            <TabsTrigger value="reviews" data-testid="reviews-tab" className="flex items-center gap-2 min-w-0">
              <MessageSquare className="w-4 h-4" />
              <span className="hidden sm:inline truncate">Reseñas</span>
            </TabsTrigger>
            <TabsTrigger value="import-folder" data-testid="import-folder-tab" className="flex items-center gap-2 min-w-0">
              <Plus className="w-4 h-4" />
              <span className="hidden sm:inline truncate">Importar Carpeta</span>
            </TabsTrigger>
          </TabsList>

          {/* Content Tab */}
          <TabsContent value="content">
            <Card className="bg-slate-800/50 border-white/10 mb-6">
              <CardHeader>
                <CardTitle className="text-white">Buscar y Agregar Nuevo Contenido</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  <div className="flex flex-col sm:flex-row gap-2 items-stretch">
                    <Input
                      data-testid="content-search-input"
                      type="text"
                      placeholder="Buscar"
                      value={searchQuery}
                      onChange={(e) => setSearchQuery(e.target.value)}
                      onKeyPress={(e) => e.key === "Enter" && handleSearchContent()}
                      className="bg-slate-900/50 border-white/10 text-white flex-1 min-w-0 w-full"
                    />
                    <Select value={contentType} onValueChange={setContentType}>
                      <SelectTrigger data-testid="content-type-select" className="w-full sm:w-28 bg-slate-900/50 border-white/10 text-white">
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent className="bg-slate-900 border-white/10">
                        <SelectItem value="movie">Película</SelectItem>
                        <SelectItem value="tv">Serie</SelectItem>
                        <SelectItem value="novela">Novela</SelectItem>
                      </SelectContent>
                    </Select>
                    <Button
                      data-testid="search-content-button"
                      onClick={handleSearchContent}
                      disabled={searching}
                      className="bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-600 hover:to-blue-600 flex items-center justify-center w-full sm:w-auto"
                    >
                      <svg xmlns="http://www.w3.org/2000/svg" className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-4.35-4.35M10.5 18a7.5 7.5 0 1 1 0-15 7.5 7.5 0 0 1 0 15z" /></svg>
                      <span className="hidden sm:inline ml-2">{searching ? "Buscando..." : "Buscar"}</span>
                    </Button>
                  </div>

                  {/* Search Results */}
                  {searchResults.length > 0 && (
                    <div className="max-h-96 overflow-y-auto overflow-x-hidden space-y-2">
                      <p className="text-sm text-gray-400 mb-2">Resultados de búsqueda:</p>
                      {searchResults.map((result) => (
                        <div
                          key={result.id}
                          className="flex items-center justify-between p-3 bg-white/5 rounded-lg border border-white/10"
                        >
                          <div className="flex items-center gap-3 min-w-0">
                            {result.poster_path && (
                              <img
                                src={`https://image.tmdb.org/t/p/w92${result.poster_path}`}
                                alt={result.title || result.name}
                                className="w-12 h-16 object-cover rounded"
                              />
                            )}
                            <div className="min-w-0">
                              <p className="font-semibold text-white truncate">{result.title || result.name}</p>
                              <p className="text-sm text-gray-400 truncate">
                                {result.release_date || result.first_air_date} - ⭐ {result.vote_average?.toFixed(1)}
                              </p>
                            </div>
                          </div>
                          <Button
                            data-testid={`add-result-${result.id}`}
                            onClick={() => handleAddContent(result.id)}
                            size="sm"
                            className="bg-gradient-to-r from-green-500 to-emerald-500 hover:from-green-600 hover:to-emerald-600"
                          >
                            <Plus className="w-4 h-4" />
                            <span className="hidden sm:inline ml-2">Agregar</span>
                          </Button>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>

            <Card className="bg-slate-800/50 border-white/10">
              <CardHeader>
                <CardTitle className="text-white">Contenido Existente ({content.length})</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  <div className="mb-3">
                    <Input
                      data-testid="existing-content-filter"
                      type="text"
                      placeholder="Buscar en contenido existente"
                      value={existingFilter}
                      onChange={(e) => setExistingFilter(e.target.value)}
                      className="bg-slate-900/50 border-white/10 text-white w-full sm:w-80 min-w-0"
                    />
                  </div>
                  {content
                    .filter((item) =>
                      item.title
                        ? item.title.toLowerCase().includes(existingFilter.toLowerCase())
                        : false
                    )
                    .map((item) => (
                    <div
                      key={item.id}
                      className="flex items-center justify-between p-4 bg-white/5 rounded-lg border border-white/10 min-w-0"
                    >
                      <div className="min-w-0">
                        <p className="font-semibold text-white truncate">{item.title}</p>
                        <p className="text-sm text-gray-400 truncate">
                          {item.content_type} - TMDB ID: {item.tmdb_id}
                        </p>
                      </div>
                      <Button
                        data-testid={`delete-content-${item.id}`}
                        onClick={() => handleDeleteContent(item.id)}
                        variant="ghost"
                        size="sm"
                        className="text-red-400 hover:text-red-300 hover:bg-red-500/10"
                      >
                        <Trash2 className="w-4 h-4" />
                      </Button>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Users Tab */}
          <TabsContent value="users">
            <Card className="bg-slate-800/50 border-white/10">
              <CardHeader>
                <CardTitle className="text-white">Usuarios ({users.length})</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {users.map((u) => (
                    <div
                      key={u.id}
                      className="flex items-center justify-between p-4 bg-white/5 rounded-lg border border-white/10 min-w-0"
                    >
                      <div className="min-w-0">
                        <p className="font-semibold text-white truncate">{u.email}</p>
                        <p className="text-sm text-gray-400 truncate">Rol: {u.role}</p>
                      </div>
                      <div className="flex gap-2 items-center">
                        <Select
                          value={u.role}
                          onValueChange={(newRole) => handleUpdateUserRole(u.id, newRole)}
                        >
                          <SelectTrigger data-testid={`user-role-select-${u.id}`} className="w-24 sm:w-32 bg-slate-900/50 border-white/10 text-white">
                            <SelectValue />
                          </SelectTrigger>
                          <SelectContent className="bg-slate-900 border-white/10">
                            <SelectItem value="user">Usuario</SelectItem>
                            <SelectItem value="admin">Admin</SelectItem>
                          </SelectContent>
                        </Select>
                        <Button
                          data-testid={`delete-user-${u.id}`}
                          onClick={() => handleDeleteUser(u.id)}
                          variant="ghost"
                          size="sm"
                          className="text-red-400 hover:text-red-300 hover:bg-red-500/10"
                        >
                          <Trash2 className="w-4 h-4" />
                        </Button>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Reviews Tab */}
          <TabsContent value="reviews">
            <Card className="bg-slate-800/50 border-white/10">
              <CardHeader>
                <CardTitle className="text-white">Reseñas ({reviews.length})</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {reviews.map((review) => (
                    <div
                      key={review.id}
                      className="flex flex-col sm:flex-row items-start sm:items-start justify-between p-4 bg-white/5 rounded-lg border border-white/10 min-w-0"
                    >
                      <div className="flex-1 min-w-0">
                        <p className="hidden sm:block font-semibold text-white truncate">{review.user_email}</p>
                        <p className="text-sm text-yellow-400 mb-2">⭐ {review.rating}/5</p>
                        <p className="text-gray-300 truncate">{review.review_text}</p>
                      </div>
                      <div className="mt-3 sm:mt-0">
                        <Button
                          data-testid={`admin-delete-review-${review.id}`}
                          onClick={() => handleDeleteReview(review.id)}
                          variant="ghost"
                          size="sm"
                          className="text-red-400 hover:text-red-300 hover:bg-red-500/10"
                        >
                          <Trash2 className="w-4 h-4" />
                        </Button>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Importar desde carpeta */}
          <TabsContent value="import-folder">
            <Card className="bg-slate-800/50 border-white/10 mb-6">
              <CardHeader>
                <CardTitle className="text-white">Importar desde carpeta</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {/* Input de carpeta con mejor estilo */}
                  <div className="flex flex-col sm:flex-row gap-2 items-center mb-4">
                    <label htmlFor="folder-upload" className="bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-600 hover:to-blue-600 text-white px-4 py-2 rounded cursor-pointer font-semibold shadow">
                      Seleccionar carpeta
                    </label>
                    <input
                      id="folder-upload"
                      type="file"
                        webkitdirectory="true"
                      multiple
                      onChange={handleFolderSelect}
                      className="hidden"
                    />
                    <span className="text-white text-sm mt-2 sm:mt-0">
                      {folderEntries.length > 0 ? `${folderEntries.length} carpeta(s) seleccionada(s)` : "Ninguna carpeta seleccionada"}
                    </span>
                  </div>
                  <div className="flex flex-col sm:flex-row gap-2 items-stretch mb-2">
                    <Select value={importContentType} onValueChange={setImportContentType}>
                      <SelectTrigger className="w-full sm:w-40 bg-slate-900/50 border-white/10 text-white">
                        <SelectValue placeholder="Tipo de contenido" />
                      </SelectTrigger>
                      <SelectContent className="bg-slate-900 border-white/10">
                        <SelectItem value="movie">Película</SelectItem>
                        <SelectItem value="tv">Serie</SelectItem>
                        <SelectItem value="novela">Novela</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                  {folderEntries.length > 0 && (
                    <>
                      <div className="mb-2">
                        {/* Botón para limpiar la lista de carpetas */}
                        <Button
                          variant="outline"
                          className="text-white border-white/20 hover:bg-white/10"
                          onClick={() => setFolderEntries([])}
                        >
                          Limpiar lista
                        </Button>
                      </div>
                      <div className="space-y-2">
                        <p className="text-gray-300 text-sm mb-2">Carpetas encontradas:</p>
                        {folderEntries
                          .map((entry, idx) => (
                            <div key={idx} className="flex items-center gap-2 bg-slate-900/30 rounded p-2">
                              <span className="text-white flex-1 truncate">{entry.name}</span>
                              <Button
                                size="sm"
                                className="bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-600 hover:to-blue-600"
                                onClick={() => searchTmdbByName(entry.name)}
                              >
                                Buscar en TMDB
                              </Button>
                            </div>
                          ))}
                      </div>
                    </>
                  )}
                  {pendingTmdbSearch && (
                    <div className="mt-4">
                      <p className="text-white font-semibold mb-2">Resultados para: {pendingTmdbSearch}</p>
                      {tmdbSearchLoading ? (
                        <p className="text-gray-300">Buscando...</p>
                      ) : (
                        <div className="space-y-2 max-h-60 overflow-y-auto">
                          {tmdbSearchResults.length === 0 && <p className="text-gray-400">No se encontraron resultados.</p>}
                          {tmdbSearchResults.map((result) => (
                            <div key={result.id} className="flex items-center justify-between p-2 bg-white/10 rounded">
                              <div className="flex items-center gap-2 min-w-0">
                                {result.poster_path && (
                                  <img
                                    src={`https://image.tmdb.org/t/p/w92${result.poster_path}`}
                                    alt={result.title || result.name}
                                    className="w-10 h-14 object-cover rounded"
                                  />
                                )}
                                <div className="min-w-0">
                                  <p className="font-semibold text-white truncate">{result.title || result.name}</p>
                                  <p className="text-xs text-gray-400 truncate">
                                    {result.release_date || result.first_air_date} - ⭐ {result.vote_average?.toFixed(1)}
                                  </p>
                                </div>
                              </div>
                              <Button
                                size="sm"
                                className="bg-gradient-to-r from-green-500 to-emerald-500 hover:from-green-600 hover:to-emerald-600"
                                onClick={() => handleImportFromTmdb(result.id)}
                              >
                                Importar
                              </Button>
                            </div>
                          ))}
                        </div>
                      )}
                      <Button
                        variant="ghost"
                        size="sm"
                        className="mt-2 text-red-400 hover:text-red-300 hover:bg-red-500/10"
                        onClick={() => { setPendingTmdbSearch(null); setTmdbSearchResults([]); }}
                      >
                        Cancelar
                      </Button>
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
};

export default AdminPanel;